lor);\n  }\n  \n  .filter-checkbox{\n    display:none;\n  }\n\n  input[type=\"checkbox\"]:checked + label{\n    display:inline-block;\n    fill:var(--main-txt-color);\n  }\n  input[type=\"checkbox\"] + label{\n    display:inline-block;\n    fill:var(--neutral-color);\n  }\n\n  .settings-icon{\n    border-radius: 50%;\n    fill:var(--neutral-color);\n  }\n  \n  .settings-icon:hover{\n    fill:var(--main-txt-color);\n  }","\n\n  .dropdown {\n    --border-radius: 3px;\n    --border: 1px solid #474a4d;\n    --speed: 200ms; \n    position: absolute;\n    z-index:2;\n    top: 100px;\n    width: 115px;\n    transform: translateX(-75%);\n    background-color: var(--main-bg-color);\n    border: var(--border);\n    border-radius: var(--border-radius);\n    padding: 0rem;\n    overflow: hidden;\n\n      a {\n        color: var(--main-txt-color);\n      }\n    \n      .menu {\n        width: 100%;\n      }\n    \n      .menu-item {\n        height: 15px;\n        display: flex;\n        align-items: center;\n        border-radius: var(--border-radius);\n        transition: background var(--speed);\n        padding: 0.5rem;\n      }\n    \n      .menu-item .icon-button {\n        margin-right: 0.5rem;\n      }\n    \n    \n      .menu-item .icon-button:hover {\n        filter: none;\n      }\n    \n      .menu-item:hover {\n        background-color: #525357;\n      }\n    \n      .icon-right {\n        margin-left: auto;\n      }\n\n  }    ",".th-tweet {\n  padding: 8px 5px 8px 5px;\n  margin-bottom: 5px;\n  flex-direction: row;\n  display: flex;\n  border: 1px solid var(--main-border-color);\n  border-radius: 3px;\n  color: var(--main-txt-color);\n  background: var(--main-bg-color);\n}\n\n.th-gutter {\n  flex-grow: 0;\n}\n\n.th-gutter img {\n  width: 35px;\n  height: 35px;\n  border-radius: 50%;\n}\n\n.th-body {\n  display: flex;\n  flex-grow: 1;\n  flex-direction: column;\n  padding-left: 5px;\n  min-width: 0;\n}\n\n.th-plus-container {\n  flex-grow: 0;\n}\n\n.th-header {\n  d